<template>
<b-container class="mt-5 pt-5 mb-5">
    <h1>Meus endereços</h1>
    <b-button href="/novo-endereco" variant="success" class="mb-2"><i class="fa fa-plus mr-2"></i>Novo endereço</b-button>

<b-row class="mt-3 mb-5">
  <b-col cols="12" sm="6" class="mb-4" v-for="(address,index) in enderecos" :key="index.id">
  <b-card header-tag="header"
          footer-tag="footer"
          bg-variant="light"
          class="mt-1 mb-3">
        <h5 slot="header" class="mb-0">Endereço {{index+=1}}</h5>
        {{address.street}} • Nº {{address.number}}<br/><div v-if="address.complement">
        Complemento: {{address.complement}}<br/></div><div v-else-if="!address.complement">
        Complemento: -</div>
       	Bairro: {{address.neighborhood}}<br/>
  	    {{address.city}} • {{address.state}}<br/>
  	    CEP: {{address.cep}}
        <em slot="footer">
          <b-button @click="populateEdit(address)" variant="primary" class="mr-2"><i class="fa fa-caret-right pr-1"></i> Alterar</b-button>
          <b-button @click="populateDelete(address)" variant="danger"><i class="fa fa-ban pr-1"></i> Excluir</b-button>
        </em>
  </b-card>
  </b-col>
</b-row>

  <!-- Edit Address Modal -->
    <b-modal id="modalEdit"
                ref="modalEdit"
                size="lg"
                title="Alterar Endereço"
                title-tag="h4"
                ok-title="Editar"
                cancel-variant="danger"
                cancel-title="Cancelar"
                @ok="editAddress(address)">
    <b-form>
   <div class="row">
			<div class="form-group col-lg-2">
        <the-mask mask="#####-###" @blur.native="searchAddress()" v-model="address.cep" class="form-control" type="text" placeholder="Seu CEP"></the-mask>
			</div>
			<div class="form-group col-lg-8 pl-0">
				<input type="text" v-model="address.street" class="form-control" placeholder="Nome da rua ou Avenida">
			</div>
      <div class="form-group col-lg-2 pl-0">
        <input type="text" v-model="address.number" class="form-control" placeholder="Nº">
      </div>
		</div>
		<div class="row">
			<div class="form-group col-lg-3">
          <input type="text" v-model="address.complement" class="form-control" placeholder="Casa/Apartamento">
			</div>
			<div class="form-group col-lg-6 pl-0">
				<input type="text" v-model="address.neighborhood" class="form-control" placeholder="Bairro">
			</div>
		</div>
		<div class="row">
			<div class="form-group col-lg-3">
				<label for="State">Estado</label>
        <b-select v-model="address.state" size="lg" :options="listStates" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
			</div>
			<div class="form-group col-lg-6 pl-0">
				<label for="City">Cidade</label>
        <b-select v-model="address.city" size="lg" :options="listCities(address.state)" :class="[{ disabled: disableFields }]"></b-select>
			</div>
		</div>
    </b-form>
    </b-modal>
 
  <!-- ./Edit Address Modal -->

  <!-- Delete Address Modal -->  
    <b-modal id="modalDelete" ref="modalDelete" title="Exclusão de Endereço" ok-title="Sim" cancel-variant="danger" cancel-title="Não" @ok="deleteAddress()">
      <p class="my-2 text-center">Você tem certeza que deseja excluir ?</p>
    </b-modal>
  <!-- ./Delete Addres Modal -->

    <!-- Modal Success Delete Address -->
  <b-modal ref="DeleteSuccess" title="Exclusão de Endereço" ok-only @ok="refreshPage">
    Endereço Deletado com Sucesso!
  </b-modal>
  <!-- ./Modal Success Delete Address -->

      <!-- Modal Success Edit Address -->
  <b-modal ref="EditSuccess" title="Editar Endereço" ok-only @ok="refreshPage">
    Endereço Editado com Sucesso!
  </b-modal>
  <!-- ./Modal Success Edit Address -->

</b-container>
</template>

<script>
import { TheMask } from 'vue-the-mask'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      disableFields: true,
      showAddress: false,
      enderecos: [],
      address: [],
      id: 0
    }
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF'
    })
  },
  components: {
    TheMask
  },
  methods: {
    async searchAddress () {
      let token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjoie1wiZW1haWxcIjpcImFkbWluQGdtYWlsLmNvbVwiLFwicGFzc3dvcmRcIjpcIjU1ZGM4N1wifSIsImlhdCI6MTUyNDU5MzQ2NCwiZXhwIjoxNTI1MTk4MjY0fQ.NduliQ3gkK4R4KMszVfWpJ9j9-VORkXGGXQsdfELDUQ'
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
      let response = await _axios.get(
        'correios/cep/' + this.address.cep
      )
      this.address.state = response.data.data.uf
      this.address.neighborhood = response.data.data.bairro
      this.address.cep = response.data.data.cep
      this.address.street = response.data.data.logradouro
      this.address.city.name = response.data.data.localidade
      this.address.city.uf = response.data.data.uf
    },
    async getAddress () {
      let response = []
      let token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjoie1wiZW1haWxcIjpcImFkbWluQGdtYWlsLmNvbVwiLFwicGFzc3dvcmRcIjpcIjU1ZGM4N1wifSIsImlhdCI6MTUyNDU5MzQ2NCwiZXhwIjoxNTI1MTk4MjY0fQ.NduliQ3gkK4R4KMszVfWpJ9j9-VORkXGGXQsdfELDUQ'
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
        response = await _axios.get(
          'address'
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async editAddress (address) {
      let response = []
      let token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjoie1wiZW1haWxcIjpcImFkbWluQGdtYWlsLmNvbVwiLFwicGFzc3dvcmRcIjpcIjU1ZGM4N1wifSIsImlhdCI6MTUyNDU5MzQ2NCwiZXhwIjoxNTI1MTk4MjY0fQ.NduliQ3gkK4R4KMszVfWpJ9j9-VORkXGGXQsdfELDUQ'
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
      response = await _axios.put(
        'address', {
          id: this.id,
          street: address.street,
          complement: address.complement,
          number: address.number,
          neighborhood: address.neighborhood,
          city: address.city.name,
          state: address.state,
          cep: address.cep
        })
      if (response.status === 200) {
        this.$refs.EditSuccess.show()
      }
    },
    async deleteAddress () {
      let response = []
      let token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjoie1wiZW1haWxcIjpcImFkbWluQGdtYWlsLmNvbVwiLFwicGFzc3dvcmRcIjpcIjU1ZGM4N1wifSIsImlhdCI6MTUyNDU5MzQ2NCwiZXhwIjoxNTI1MTk4MjY0fQ.NduliQ3gkK4R4KMszVfWpJ9j9-VORkXGGXQsdfELDUQ'
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
      response = await _axios.delete(
        'address/' + this.id
      )
      if (response.status === 200) {
        this.$refs.DeleteSuccess.show()
      }
    },
    populateEdit (address) {
      this.address = JSON.parse(JSON.stringify(address))
      this.address.city = { 'name': this.address.city, 'uf': this.address.state }
      this.id = this.address.id
      this.$refs.modalEdit.show()
    },
    populateDelete (address) {
      this.address = JSON.parse(JSON.stringify(address))
      this.id = this.address.id
      this.$refs.modalDelete.show()
    },
    refreshPage () {
      location.reload()
    }
  },
  mounted () {
    this.getAddress().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response) => {
      this.enderecos = response
      this.address = response
    })
  }
}
</script>

<style scoped>

</style>




