<template>
<b-container class="mt-5 pt-5 mb-5">
  <b-button to="/enderecos" variant="info"><i class="fa fa-arrow-left mr-2"></i>Meus Endereços</b-button>
<div v-if="!noHaveCep">
  <h3 align="center">Buscar pelo CEP</h3>

    <b-form>
  
      <b-form-group id="exampleInputGroup1"
                    label="CEP:"
                    label-for="exampleInput1"
                    description="Preencha seu CEP">
          <the-mask id="exampleInput1"
                    mask="#####-###"
                    v-model="address.cep"
                    @blur.native="searchAddress()"
                    class="form-control"
                    type="text"
                    required
                    placeholder="Seu CEP">
          </the-mask>
      </b-form-group>
      <b-form-group id="exampleInputGroup2"
                    label="Número:"
                    label-for="exampleInput2">
        <b-form-input id="exampleInput2"
                      type="text"
                      required
                      v-model="address.number"
                      placeholder="Número">
        </b-form-input>
      </b-form-group>
      <b-form-group id="exampleInputGroup3"
                    label="Complemento:"
                    label-for="exampleInput3">
        <b-form-input id="exampleInput3"
                      type="text"
                      v-model="address.complement"
                      placeholder="Complemento">
        </b-form-input>
      </b-form-group>

      <b-button @click="populate(address)" block variant="theme">BUSCAR</b-button>
      <b-button @click="noHaveCep = true" variant="link" block>NÃO SEI MEU CEP</b-button>

    </b-form>
</div>

<div v-if="noHaveCep">
  <h3 align="center">Não sei meu CEP</h3>

    <b-form>
  
      <b-form-group id="exampleInputGroup1"
                    label="Cidade:"
                    label-for="exampleInput1">
      <b-select v-model="auxAddress.city" size="lg" :options="listCities(auxAddress.city.uf)" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
      </b-form-group>
      <b-form-group id="exampleInputGroup2"
                    label="Endereço de Entrega:"
                    label-for="exampleInput2">
        <b-form-input id="exampleInput2"
                      type="text"
                      v-model="auxAddress.street"
                      placeholder="Nome da Rua ou Avenida">
        </b-form-input>
      </b-form-group>
      <b-form-group id="exampleInputGroup3"
                    label="Bairro:"
                    label-for="exampleInput3">
        <b-form-input id="exampleInput3"
                      type="text"
                      v-model="auxAddress.neighborhood"
                      placeholder="Bairro">
        </b-form-input>
      </b-form-group>
      <b-form-group id="exampleInputGroup4"
                    label="Número:"
                    label-for="exampleInput4">
        <b-form-input id="exampleInput4"
                      type="number"
                      v-model="auxAddress.number"
                      placeholder="Nº">
        </b-form-input>
      </b-form-group>
      <b-form-group id="exampleInputGroup5"
                    label="Complemento:"
                    label-for="exampleInput5">
        <b-form-input id="exampleInput5"
                      type="text"
                      v-model="auxAddress.complement"
                      placeholder="Casa/Apartamento">
        </b-form-input>
      </b-form-group>

      <b-button @click="populateNoCep(auxAddress)" block variant="theme">BUSCAR</b-button>
      <b-button @click="noHaveCep = false" variant="link" block>LEMBREI MEU CEP</b-button>

    </b-form>

</div>

<!-- New Address Modal -->
    <b-modal id="modalAdd"
                ref="modalAdd"
                size="lg"
                title="Adicionar Endereço"
                title-tag="h4"
                ok-title="Registrar"
                @ok="saveAddress(address)"
                cancel-variant="danger"
                cancel-title="Cancelar">
        <b-form @submit.stop.prevent="handleAddModal">
   <div class="row">
			<div class="form-group col-lg-2">
        <the-mask mask="#####-###" v-model="address.cep" class="form-control" type="text" placeholder="Seu CEP"></the-mask>
			</div>
			<div class="form-group col-lg-8 pl-0">
				<input type="text" v-model="address.street" class="form-control"  placeholder="Nome da rua ou Avenida">
			</div>
      <div class="form-group col-lg-2 pl-0">
        <input type="number" v-model="address.number" class="form-control" placeholder="Nº">
      </div>
		</div>
		<div class="row">
			<div class="form-group col-lg-3">
          <input type="text" v-model="address.complement" class="form-control" placeholder="Casa/Apartamento">
			</div>
			<div class="form-group col-lg-6 pl-0">
				<input type="text" v-model="address.neighborhood" class="form-control" placeholder="Bairro">
			</div>
		</div>
		<div class="row">
			<div class="form-group col-lg-3">
				<label for="UF">Estado</label>
        <b-select v-model="address.city.uf" size="lg" :options="listStates" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
			</div>
			<div class="form-group col-lg-6 pl-0">
				<label for="Cidade">Cidade</label>
        <b-select v-model="address.city" size="lg" :options="listCities(address.city.uf)" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
			</div>
		</div>
        </b-form>
    </b-modal>
  <!-- ./New Address Modal -->

<!-- Modal Success New Address -->
  <b-modal ref="NewAddressSuccess" title="Cadastro de Endereço" ok-only>
    Endereço Cadastrado com Sucesso!
  </b-modal>
<!-- ./Modal Success New Address -->

  <!-- New No Cep Address Modal -->
    <b-modal id="modalAddNoCep"
                ref="modalAddNoCep"
                size="lg"
                title="Adicionar Endereço"
                title-tag="h4"
                ok-title="Registrar"
                cancel-variant="danger"
                cancel-title="Cancelar">
        <b-form>
   <div class="row">
			<div class="form-group col-lg-2">
        <the-mask mask="#####-###" v-model="auxAddress.cep" class="form-control" type="text" placeholder="Seu CEP"></the-mask>
			</div>
			<div class="form-group col-lg-8 pl-0">
				<input type="text" v-model="auxAddress.street" class="form-control"  placeholder="Nome da rua ou Avenida">
			</div>
      <div class="form-group col-lg-2 pl-0">
        <input type="number" v-model="auxAddress.number" class="form-control" placeholder="Nº">
      </div>
		</div>
		<div class="row">
			<div class="form-group col-lg-3">
          <input type="text" v-model="auxAddress.complement" class="form-control" placeholder="Casa/Apartamento">
			</div>
			<div class="form-group col-lg-6 pl-0">
				<input type="text" v-model="auxAddress.neighborhood" class="form-control" placeholder="Bairro">
			</div>
		</div>
		<div class="row">
			<div class="form-group col-lg-3">
				<label for="UF">Estado</label>
        <b-select v-model="auxAddress.city.uf" size="lg" :options="listStates" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
			</div>
			<div class="form-group col-lg-6 pl-0">
				<label for="Cidade">Cidade</label>
        <b-select v-model="auxAddress.city" size="lg" :options="listCities(auxAddress.city.uf)" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
			</div>
		</div>
        </b-form>
    </b-modal>
  <!-- ./New No Cep Address Modal -->

</b-container>
</template>

<script>
import { TheMask } from 'vue-the-mask'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      disableFields: true,
      noHaveCep: false,
      address: {
        cep: '',
        uf: '',
        city: {name: '', uf: ''},
        neighborhood: '',
        street: '',
        number: '',
        complement: ''
      },
      auxAddress: {
        cep: '',
        uf: 'MG',
        city: {name: 'Uberlândia', uf: 'MG'},
        neighborhood: '',
        street: '',
        number: '',
        complement: ''
      }
    }
  },
  components: {
    TheMask
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF'
    })
  },
  methods: {
    async searchAddress () {
      let token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjoie1wiZW1haWxcIjpcImFkbWluQGdtYWlsLmNvbVwiLFwicGFzc3dvcmRcIjpcIjU1ZGM4N1wifSIsImlhdCI6MTUyNDUwNDM5NywiZXhwIjoxNTI1MTA5MTk3fQ.A6KdYJAzckRS3WeohXDX1iKHKa4ZRwHbp68IzRba-Ic'
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
      let cep = this.address.cep
      let response = await _axios.get(
        'correios/cep/' + cep
      )
      let _address = response.data.data
      this.address = {
        cep: _address.cep,
        city: { name: _address.localidade, uf: _address.uf },
        neighborhood: _address.bairro,
        street: _address.logradouro,
        number: '',
        complement: ''
      }
    },
    async saveAddress (address) {
      let response = []
      let token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJkYXRhIjoie1wiZW1haWxcIjpcImFkbWluQGdtYWlsLmNvbVwiLFwicGFzc3dvcmRcIjpcIjU1ZGM4N1wifSIsImlhdCI6MTUyNDUwNDM5NywiZXhwIjoxNTI1MTA5MTk3fQ.A6KdYJAzckRS3WeohXDX1iKHKa4ZRwHbp68IzRba-Ic'
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
      response = await _axios.post(
        'address', {
          street: address.street,
          complement: address.complement,
          number: address.number,
          neighborhood: address.neighborhood,
          city: address.city.name,
          state: address.city.uf,
          cep: address.cep
        })
      if (response.status === 200) {
        this.$refs.NewAddressSuccess.show()
      }
    },
    populate (address) {
      if (!this.address.cep && !this.address.number) {
        alert('CEP e Número são obrigatórios!')
      } else if (!this.address.number) {
        alert('Número é obrigatório!')
      } else if (!this.address.cep) {
        alert('CEP é obrigatório!')
      } else {
        this.address = JSON.parse(JSON.stringify(address))
        this.$refs.modalAdd.show()
      }
    },
    handleOkAddModal (address) {
      if (!this.address.cep && !this.address.street && !this.address.number && !this.address.neighborhood) {
        alert('Preencha os dados obrigatórios!')
      } else if (!this.address.cep) {
        alert('CEP é obrigatório!')
      } else if (!this.address.street) {
        alert('Nome da Rua ou avenida é obrigatório!')
      } else if (!this.address.number) {
        alert('Número é obrigatório!')
      } else if (!this.address.neighborhood) {
        alert('Bairro é obrigatório!')
      } else {
        this.saveAddress(address)
      }
    },
    async populateNoCep (auxAddress) {
      let address = JSON.parse(JSON.stringify(auxAddress))
      address = encodeURI(auxAddress.street + ' ' + auxAddress.number + ' ' + auxAddress.neighborhood + ' ' + auxAddress.city.name + ' ' + auxAddress.city.uf)
      console.log(address)
      let response = []
      let _axios = this.$http.create()
      response = await _axios.get('https://maps.googleapis.com/maps/api/geocode/json?address=' + address + '&key=AIzaSyD1PPrTt9S6nYT-wPFs0q4mIpbY3XyAjqA')
      console.log(JSON.parse(response))
      // this.$refs.modalAddNoCep.show()
    }
  }
}
</script>

<style scoped>

</style>



