<template>
    <!-- top Links -->
    <div class="top-links">
        <div class="container">
            <b-nav fill class="justify-content-center links">
                <b-nav-item :class="['link-item', { active: item.isActive }]" :to="item.path" v-for="item in items" :key="item.id">
                    <span>{{item.id}}</span> {{item.description}}
                </b-nav-item>
            </b-nav>
        </div>
    </div>
    <!-- end:Top links -->
</template>

<script>
export default {
  data () {
    return {}
  },
  computed: {
    items () {
      let path = this.$route.path.substr(1)
      let aux = [
        { id: 1, description: 'Escolha a sua localização', path: {name: 'home'}, isActive: true },
        { id: 2, description: 'Escolha o restaurante', path: {name: 'restaurants'}, isActive: false },
        { id: 3, description: 'Escolha a sua comida favorita', path: {name: 'profile', params: { id: 1 }}, isActive: false },
        { id: 4, description: 'Encomendar e pagar', path: {name: 'checkout'}, isActive: false }
      ]
      return aux.map((value, index) => {
        let isActive = value.path === path
        if (isActive) {
          value.path = ''
          value.isActive = isActive
        }
        return value
      })
    }
  },
  methods: {}
}
</script>

<style scoped>
/********* top links part ***********/

.top-links {
  padding: 15px 0;
  border-bottom: 1px solid #eaebeb;
}

.top-links ul {
  margin-bottom: 0;
}

.link-item a {
  font-size: 12px;
  color: #414551;
}

.link-item.active span {
  background-color: #ffb42d;
  color: #fff;
}

.links span {
  border: 1px solid #eaebeb;
  border-radius: 100px;
  width: 32px;
  height: 32px;
  color: #3a3e47;
  font-weight: 600;
  display: inline-block;
  margin-right: 18px;
  text-align: center;
  line-height: 28px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.links li:hover span {
  background-color: #ffb42d;
  color: #fff;
}

@media (max-width: 800px) {
  .top-links {
    display: none;
  }
}
</style>


